//SET_TEXT_FILTER
export const setTextFilter = (text = "") => ({
  type: "SET_TEXT_FILTER",
  text,
});
//SORT_BY_DATE
export const sortByDate = () => ({
  type: "SORT_BY_DATE",
});
//"SORT_BY_PATIENTS_NAME"
export const sortByPatientsName = () => ({
  type: "SORT_BY_PATIENTS_NAME",
});
//SET_START_DATE
export const setStartDate = (startDate) => ({
  type: "SET_START_DATE",
  startDate,
});
//SET_END_DATE
export const setEndDate = (endDate) => ({
  type: "SET_END_DATE",
  endDate,
});
